import { createApp } from 'vue'
import * as Vue from 'vue'
import App from './App.vue'
import router from './router'

// Element Plus
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import zhCn from 'element-plus/es/locale/lang/zh-cn'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'

const app = createApp(App)

// 
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

// Element Plus
app.use(router)

// Element Plus
app.use(ElementPlus, {
  locale: zhCn,
  size: 'default',
  zIndex: 3000,
  // 
  autoInsertSpace: false
})

// 
app.config.errorHandler = (err, vm, info) => {
  console.group('');
  console.error(':', err);
  console.error(':', info);
  
  // 
  if (vm) {
    console.log(':', vm.$options.name || '');
    console.log(':', vm.$route ? vm.$route.path : '');
    
    // 
    try {
      console.log(':', JSON.stringify(vm.$data, null, 2));
    } catch (e) {
      console.warn(':', e);
    }
  }
  
  // 
  try {
    const ElementPlus = app.config.globalProperties.$ELEMENT;
    if (ElementPlus && ElementPlus.Message) {
      ElementPlus.Message.error('');
    }
  } catch (displayError) {
    console.error(':', displayError);
  }
  
  console.groupEnd();
};

// Promise
window.addEventListener('unhandledrejection', (event) => {
  console.group('Promise');
  console.error(':', event.reason);
  console.error('Promise:', event.promise);
  console.groupEnd();
});

// 
window.addEventListener('error', (event) => {
  console.group('JavaScript');
  console.error(':', event.message);
  console.error(':', event.filename);
  console.error(':', event.lineno);
  console.error(':', event.colno);
  console.error(':', event.error);
  console.groupEnd();
});

// Element PlusResizeObserver
try {
  // ResizeObserver
  window.addEventListener('error', (event) => {
    if (event.message && event.message.includes('ResizeObserver') && event.message.includes('loop')) {
      console.warn('ResizeObserver', event);
      event.stopPropagation();
      event.preventDefault();
      return false;
    }
  }, true);
  
  // getComputedStyle
  const originalGetComputedStyle = window.getComputedStyle;
  window.getComputedStyle = function(element, pseudoElt) {
    if (!element || element.nodeType !== 1) {
      console.warn('DOMgetComputedStyle', element);
      return {}; // 
    }
    return originalGetComputedStyle(element, pseudoElt);
  };
} catch (e) {
  console.error('', e);
}

app.mount('#app')
