<script>
import { ref, onMounted, computed } from 'vue'
import axios from 'axios'
import { ElMessage, ElMessageBox, ElLoading } from 'element-plus'
import { useRoute, useRouter } from 'vue-router'

// axios
axios.defaults.headers.common['Accept'] = 'application/json'
axios.defaults.headers.common['Content-Type'] = 'application/json;charset=UTF-8'

export default {
  name: 'App',
  setup() {
    const crawlerForm = ref({
      url: ''
    })
    const crawling = ref(false)
    const searchKeyword = ref('')
    const articles = ref([])
    const route = useRoute()
    const router = useRouter()
    const activeRoute = computed(() => route.path)

    // 
    const fixElementPlusMenus = () => {
      console.log(':', activeRoute.value);

      setTimeout(() => {
        try {
          // 
          const menuItems = document.querySelectorAll('.el-menu-item');
          if (menuItems && menuItems.length > 0) {
            menuItems.forEach(item => {
              // DOM
              if (item && item.nodeType === 1) {
                // 
                item.style.minWidth = '80px';
                item.style.padding = '0 20px';

                // index
                const itemPath = item.getAttribute('index');
                if (itemPath === activeRoute.value) {
                  console.log(':', itemPath);
                  // 
                  item.classList.add('is-active');
                } else if (activeRoute.value.startsWith('/article/') && itemPath === '/') {
                  // 
                  item.classList.add('is-active');
                } else {
                  // 
                  item.classList.remove('is-active');
                }
              }
            });
            console.log('');
          } else {
            console.warn('');
          }
        } catch (err) {
          console.error('', err);
        }
      }, 500); // 500msDOM
    };

    // 
    const setupMenuRefreshListener = () => {
      window.addEventListener('menu-refresh', () => {
        console.log('');
        fixElementPlusMenus();
      });
    }

    // 
    const setupRouteListener = () => {
      router.afterEach((to, from) => {
        console.log(`: ${from.path} -> ${to.path}`);
        // 
        setTimeout(() => {
          fixElementPlusMenus();
        }, 100);
      });
    }

    // 
    const formatDate = (dateStr) => {
      if (!dateStr) return '';
      try {
        const date = new Date(dateStr);
        if (isNaN(date.getTime())) {
          // 
          return dateStr;
        }
        return new Intl.DateTimeFormat('zh-CN', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit',
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit',
          hour12: false
        }).format(date);
      } catch (error) {
        console.error(':', error);
        return dateStr;
      }
    }

    // 
    const crawlArticle = async () => {
      if (!crawlerForm.value.url) {
        ElMessage.warning('URL')
        return
      }

      crawling.value = true
      try {
        const response = await axios.post('/api/crawler/crawl', {
          url: crawlerForm.value.url
        })
        ElMessage.success(response.data)
        crawlerForm.value.url = ''
        loadArticles() // 
      } catch (error) {
        ElMessage.error(error.response?.data || '')
      } finally {
        crawling.value = false
      }
    }

    // 
    const searchArticles = async () => {
      try {
        if (!searchKeyword.value.trim()) {
          // bug
          await loadArticles();
          return;
        }
        const response = await axios.get(`/api/search?keyword=${encodeURIComponent(searchKeyword.value.trim())}`);
        articles.value = response.data;
        if (articles.value.length === 0) {
          ElMessage.info('');
        }
      } catch (error) {
        console.error(':', error);
        ElMessage.error('' + (error.response?.data || error.message));
      }
    }

    // 
    const loadArticles = async () => {
      try {
        const response = await axios.get('/api/crawler/articles')
        articles.value = response.data
      } catch (error) {
        ElMessage.error('')
      }
    }

    // 
    const deleteArticle = async (article) => {
      try {
        if (!article || !article.url) {
          ElMessage.error('');
          return;
        }

        // 
        const confirmResult = await ElMessageBox.confirm(
          `"${article.title}"`,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        );

        if (confirmResult !== 'confirm') {
          return;
        }

        // URLURL
        const urls = article.url.split(',');
        const firstUrl = urls[0] || '';
        if (!firstUrl) {
          ElMessage.error('URL');
          return;
        }

        // URL
        const encodedUrl = encodeURIComponent(encodeURIComponent(firstUrl));

        // 
        const loading = ElLoading.service({
          lock: true,
          text: '...',
          background: 'rgba(0, 0, 0, 0.7)'
        });

        try {
          const response = await axios.delete(`/api/crawler/articles/${encodedUrl}`);

          if (response.status === 200) {
            ElMessage.success('');
            // 
            const index = articles.value.findIndex(a => a.url === article.url);
            if (index !== -1) {
              articles.value.splice(index, 1);
            }
          } else {
            ElMessage.warning(response.data || '');
          }
        } finally {
          loading.close();
        }
      } catch (error) {
        console.error(':', error);

        if (error.response?.data) {
          // HTML
          const errorText = error.response.data.includes('<!doctype html>')
            ? ''
            : error.response.data;
          ElMessage.error(`${errorText}`);
        } else if (error.request) {
          ElMessage.error('');
        } else {
          ElMessage.error(`${error.message}`);
        }
      }
    }

    // 
    const handleMenuSelect = (index) => {
      console.log(':', index);

      // 
      if (route.path.startsWith('/article/')) {
        console.log(':', index);

        // replacepush
        router.replace(index);

        // 
        setTimeout(fixElementPlusMenus, 100);
        return;
      }

      // 
      if (index === '/search') {
        console.log('');

        // 
        if (route.path === '/search') {
          console.log('');
          return;
        }

        // reloadvue-router
        const currentUrl = window.location.href;
        const baseUrl = currentUrl.split('#')[0];
        window.location.href = baseUrl + '#/search';
        setTimeout(() => window.location.reload(), 10);
        return;
      }
    };

    // 
    onMounted(() => {
      loadArticles();
      fixElementPlusMenus();
      setupMenuRefreshListener();
      setupRouteListener();
    });

    return {
      crawlerForm,
      crawling,
      searchKeyword,
      articles,
      crawlArticle,
      searchArticles,
      deleteArticle,
      formatDate,
      activeRoute,
      handleMenuSelect
    }
  }
}
</script>

<template>
  <div id="app">
    <el-container>
      <el-header>
        <div class="header-container">
          <div class="logo"></div>
          <el-menu
            mode="horizontal"
            router
            :default-active="activeRoute"
            class="safe-el-menu"
            style="min-width: 300px;"
            @select="handleMenuSelect"
          >
            <el-menu-item index="/" style="min-width: 80px;"></el-menu-item>
            <el-menu-item index="/search" style="min-width: 80px;" class="search-menu-item"></el-menu-item>
          </el-menu>
        </div>
      </el-header>
      <el-main>
        <router-view />
      </el-main>
      <el-footer>
        <div class="footer-text">
           &copy; {{ new Date().getFullYear() }}
        </div>
      </el-footer>
    </el-container>
  </div>
</template>

